import switchc from '@/components/zz-switchc/zz-switchc.vue'

var me;
export default {
	components: {
		switchc
	},
	data() {
		return {
			menutitle: "全部",
			temp: [],
			list: ["全部", "中国文学", "外国文学", "儿童文学", "散文", "经典名著", "小说", "历史", "教育","成功励志","心灵鸡汤"],
			switcha: true,
			modalName:false,
			number:0
		};
	},
	onLoad() {
		me = this;
		this.refresh()
	},
	onPullDownRefresh() {
		this.refresh()
	},
	methods: {
		refresh() {
			uni.showLoading({
				mask: true,
				title: "请稍后..."
			});
			me.saixuan(this.number)
		},
		saixuan(number){
			var api = "";
			if(me.switcha){
				api = "/tuishu/getTuiShu";
			}else{
				api = "/qiushu/getQiushu";
			}
			uni.request({
				url: me.serverUrl+api,
				method: "GET",
				success(res) {
					var type = me.list[number];
					var result = [];
					var data1 = res.data.data;
					var a = false;
					if(type!=="全部"){
						for (var i = 0; i < data1.length; i++) {
							var tmp = data1[i].book; 
							for (var j = 0; j < tmp.tbCatalogs.length; j++) {
								if (tmp.tbCatalogs[j].catalogName === type) {
									result.push(data1[i]);
									a = true;
									if(a) {
										break;
									}
								}
							}
						}
						me.temp = result;
					}else{
						me.temp = data1;
					}
				},
				complete: () => {
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})
			
		},
		dianji(number) {
			console.log(number);
			this.number = number;
			me.saixuan(number);
			me.menutitle = me.list[number]
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		showModal() {
			this.modalName = !this.modalName;
			console.log(this.modalName)
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},

		switchchange(e) {
			uni.showLoading({
				mask: true,
				title: "请稍后..."
			});
			this.menutitle = '全部';
			console.log(e.value);
			me.switcha = e.value;
			me.saixuan(this.number)
		},

		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},
		gotoBuffer(){
			uni.navigateTo({
				url:"../buffer/buffer"
			})
		}
	}
}
